@charset "utf-8";

$breakpoint-mobile: 767px;

body {margin: 0; padding: 0;}

//PC
@media (min-width:($breakpoint-mobile + 1)) {
  .header {
    position: fixed;
    left: 0; top: 0;
    background-color: #ccc;
    width: 180px; height: 100vh;
    > h1 {
      text-align: center;
      padding: 15px 0;
    }
  }
  main {
    position: absolute;
    width: calc(100% - 180px);
    left: 180px; top: 0px;
    padding: 30px;
    box-sizing: border-box;
  }
}
//Mobile
@media (max-width:$breakpoint-mobile) {
  #root {
    padding-top: 100px;
  }
  .header {
    position: fixed;
    width: 100%;
    top: 0;
    box-shadow: 2px 1px 6px #000;
    background: #fff;
    > h1 {
      text-align: center;
    }
  }
  .article img {
    width: 100%; max-width: 500px;
  }
}

.load_img {
  position: fixed;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  transition: .5s;
  &.success {
    opacity: 0;
    visibility: hidden;
  } 
}