body {
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  .header {
    position: fixed;
    left: 0;
    top: 0;
    background-color: #ccc;
    width: 180px;
    height: 100vh;
  }
  .header > h1 {
    text-align: center;
    padding: 15px 0;
  }
  main {
    position: absolute;
    width: calc(100% - 180px);
    left: 180px;
    top: 0px;
    padding: 30px;
    box-sizing: border-box;
  }
}
@media (max-width: 767px) {
  #root {
    padding-top: 100px;
  }
  .header {
    position: fixed;
    width: 100%;
    top: 0;
    box-shadow: 2px 1px 6px #000;
    background: #fff;
  }
  .header > h1 {
    text-align: center;
  }
  .article img {
    width: 100%;
    max-width: 500px;
  }
}
.load_img {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s;
}
.load_img.success {
  opacity: 0;
  visibility: hidden;
}/*# sourceMappingURL=app.css.map */